import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import { addCategory, getCategories } from '../../../api/categories';

const initialState = {
  categories: {},
  loading: false,
}

export const fetchCategoriesAsynk = createAsyncThunk(
    "categories/fetchCategoriesAsynk",
    async (entpoint) => {
        const {data} = await getCategories(entpoint);
        
        return data;
    }
)
export const addCategoryAsynk = createAsyncThunk(
  "categories/addCategoryAsynk",
  async (data) => {
     return await addCategory(data);
  }
)

export const CategoriesSlice = createSlice({
  name: 'counter',
  initialState,
  reducers: {
    setLoading: (state) => {
      state.loading = true 
    }
  },
  extraReducers : builder => {
    builder
    .addCase(fetchCategoriesAsynk.pending, (state, action) =>{
      state.loading = true;
    })
    .addCase(fetchCategoriesAsynk.fulfilled, (state, action) =>{
        state.categories = action.payload;
        state.loading = false;
    })
    .addCase(addCategoryAsynk.fulfilled, (state, action) =>{
      state.categories.data.data.pop()
      state.categories.data.data = [action.payload.data.data,...state.categories.data.data]
    })
  }
})

export const { setLoading } = CategoriesSlice.actions

export default CategoriesSlice.reducer