import React, { useState } from 'react';
import { Button, message, Steps } from 'antd';
import { Select } from 'antd';
import ChooseService from './ChooseService';
import ChooseStylist from './ChooseStylist';
import Total from './Total';
const { Option, OptGroup } = Select;
const { Step } = Steps;
const steps = [
    {
        title: 'Chọn dịch vụ',
    },
    {
        title: 'Chọn Stylist',
    },
    {
        title: 'Đặt trước',
    },
];

const handleChange = (value) => {
    console.log(`selected ${value}`);
};

const Orders = () => {
    const [current, setCurrent] = useState(0);
    const next = () => {
        setCurrent(current + 1);
    };
    const prev = () => {
        setCurrent(current - 1);
    };
    return (
        <div className='w-full mt-20 md:mt-28 md:z-50'>
            <div className=' m-auto w-[90%] md:w-[50%] mt-1'>
                <Steps current={current}>
                    {steps.map((item) => (
                        <Step key={item.title} title={item.title} />

                    ))}
                </Steps>
                <div className="steps-content">
                    {current === 0 ? <ChooseService /> : current === 1 ? <ChooseStylist /> : <Total /> }
                </div>
                <div className="steps-action flex flex-col md:flex-row justify-around items-center">
                    {current < steps.length - 1 && (
                        <Button type="primary w-full md:w-[49.5%]" className=' w-full md:w-[50%]' onClick={() => next()}>
                            Tiếp theo
                        </Button>
                    )}
                    {current === steps.length - 1 && (
                        <Button type="primary" className=' w-full md:w-[50%]' onClick={() => message.success('Processing complete!')}>
                            Hoàn tất
                        </Button>
                    )}
                    {current > 0 && (
                        <Button
                            className='w-full md:w-[49.5%] mt-3 md:mt-0'
                            onClick={() => prev()}
                        >   
                            Quay lại
                        </Button>
                    )}
                </div>
            </div>
        </div>
    )
}

export default Orders